import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LayoutComponent } from './layout.component';
import { FranchiseAndRestaurantValidityGuard, ClaimsGuard } from '@guards';

const routes: Routes = [
  {
    path: '',
    pathMatch: 'full',
    redirectTo: 'dashboard',
  },
  {
    path: '',
    component: LayoutComponent,
    canActivate: [ClaimsGuard, FranchiseAndRestaurantValidityGuard],
    children: [
      {
        path: 'dashboard',
        loadChildren: () =>
          import('@pages/layout/dashboard/dashboard.module').then(
            (m) => m.DashboardModule
          ),
      },
      {
        path: 'menu',
        loadChildren: () =>
          import('@pages/layout/menu/menu.module').then((m) => m.MenuModule),
      },
      {
        path: 'branches',
        loadChildren: () =>
          import('@pages/layout/branches/branches.module').then(
            (m) => m.BranchesModule
          ),
      },
      {
        path: 'preferences',
        loadChildren: () =>
          import('@pages/layout/preferences/preferences.module').then(
            (m) => m.PreferencesModule
          ),
      },
      {
        path: 'reports',
        loadChildren: () =>
          import('@pages/layout/reports/reports.module').then(
            (m) => m.ReportsModule
          ),
      },
      {
        path: 'users',
        loadChildren: () =>
          import('@pages/layout/users/users.module').then((m) => m.UsersModule),
      },
      {
        path: 'income-and-payments',
        loadChildren: () =>
          import(
            '@pages/layout/income-and-payments/income-and-payments.module'
          ).then((m) => m.IncomeAndPaymentsModule),
      },
      {
        path: 'profile',
        loadChildren: () =>
          import('@pages/layout/profile/profile.module').then(
            (m) => m.ProfileModule
          ),
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class LayoutRoutingModule {}
