import { MenuOnboardingComponent } from './menu-onboarding/menu-onboarding.component';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import {
  FormControl,
  FormGroup,
  Validators,
  FormBuilder,
  FormArray,
} from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { NewMenuComponent } from './new-menu/new-menu.component';
import { NewCategoryComponent } from './new-category/new-category.component';
import { Modifiers, Menu, FoodDishes, Restaurant } from '@models';
import { MenuService, RestaurantService } from '@services';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { DeleteMenuComponent } from './delete-menu/delete-menu.component';
import { CreateModifierComponent } from './create-modifier/create-modifier.component';
import { AddModifierComponent } from './add-modifier/add-modifier.component';
import { Subscription } from 'rxjs';
@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.scss'],
  preserveWhitespaces: true,
})
export class MenuComponent implements OnInit, OnDestroy {
  public create = 'menu';
  public urlTemplate = '';
  public loaded = false;
  public uploadMenu = new FormControl('');
  public restauranteSelected: Restaurant;
  public menuData: Array<Menu>;
  public menuExpandedId: number;
  public categorySelected: number;
  public dishSelected: number;
  public nameFile = '';
  public foodDishPreview = '';
  public enlistedModifiers: Array<Modifiers> = [];
  public formFoodDish = new FormGroup({
    name: new FormControl('', [
      Validators.required,
      Validators.minLength(3),
      Validators.maxLength(100),
    ]),
    price_in_cents: new FormControl('', [
      Validators.required,
      Validators.min(1),
      Validators.pattern(/^[+]?([0-9]{0,})*[.]?([0-9]{0,2})?$/),
    ]),
    iva: new FormControl('', [
      Validators.required,
      Validators.min(3),
      Validators.max(100),
      Validators.pattern(/^[0-9]+$/),
    ]),
    description: new FormControl('', [
      Validators.required,
      Validators.minLength(4),
      Validators.maxLength(150),
    ]),
    image: new FormControl(''),
  });
  public modifiersSelected: Modifiers;
  public modifiers: Modifiers[] = [];
  public formModifier = new FormGroup({
    name: new FormControl('', Validators.required),
    question: new FormControl('', Validators.required),
    multipleChoice: new FormControl('', Validators.required),
    max: new FormControl(1, [Validators.required, Validators.min(1)]),
    min: new FormControl(1, [Validators.required, Validators.min(1)]),
    modifiersInForm: this.formBuilder.array([]),
  });
  public modifiersInForm: FormArray;
  private currentRestaurantSubscription = Subscription.EMPTY;
  private getMenuSubscription = Subscription.EMPTY;
  private uploadZipSubscription = Subscription.EMPTY;
  private newMenuModalSubscription = Subscription.EMPTY;
  private duplicateMenuSubscription = Subscription.EMPTY;
  private duplicateMatrixMenuSubscription = Subscription.EMPTY;
  private deleteMenuModalSubscription = Subscription.EMPTY;
  private orderMenuSubscription = Subscription.EMPTY;
  private getCategorySubscription = Subscription.EMPTY;
  private newCategoryModalSubscription = Subscription.EMPTY;
  private duplicateCategorySubscription = Subscription.EMPTY;
  private deleteCategorySubscription = Subscription.EMPTY;
  private orderCategorySubscription = Subscription.EMPTY;
  private getDishesSubscription = Subscription.EMPTY;
  private editDishesSubscription = Subscription.EMPTY;
  private createDishesSubscription = Subscription.EMPTY;
  private duplicateDishesSubscription = Subscription.EMPTY;
  private deleteDishesSubscrtiption = Subscription.EMPTY;
  private orderDishesSubscription = Subscription.EMPTY;
  private getModifierSubscription = Subscription.EMPTY;
  private createModifierModalSubscription = Subscription.EMPTY;
  private addModifierModalSubscription = Subscription.EMPTY;
  private createModifierSubscription = Subscription.EMPTY;
  private deleteModifierLocalSubscription = Subscription.EMPTY;
  private editModfierSubscription = Subscription.EMPTY;
  constructor(
    private menuService: MenuService,
    public dialog: MatDialog,
    private snackBar: MatSnackBar,
    private restaurantService: RestaurantService,
    private formBuilder: FormBuilder
  ) {}

  ngOnInit() {
    this.currentRestaurantSubscription = this.restaurantService.currentRestaurant.subscribe(
      (restaurant: Restaurant) => {
        this.restauranteSelected = restaurant;
        this.getMenuSubscription = this.menuService
          .getMenu(this.restauranteSelected.id)
          .subscribe((response) => {
            this.menuData = response.menus;
            if (this.menuData.length === 0) {
              this.menuService.getTemplate().subscribe((responseTemplate) => {
                this.urlTemplate = responseTemplate.template;
              });
            }
            this.loaded = true;
            this.getModifier();
          });
      }
    );
  }

  ngOnDestroy() {
    this.currentRestaurantSubscription.unsubscribe();
    this.getMenuSubscription.unsubscribe();
    this.uploadZipSubscription.unsubscribe();
    this.newMenuModalSubscription.unsubscribe();
    this.duplicateMenuSubscription.unsubscribe();
    this.duplicateMatrixMenuSubscription.unsubscribe();
    this.deleteMenuModalSubscription.unsubscribe();
    this.orderMenuSubscription.unsubscribe();
    this.getCategorySubscription.unsubscribe();
    this.newCategoryModalSubscription.unsubscribe();
    this.duplicateCategorySubscription.unsubscribe();
    this.deleteCategorySubscription.unsubscribe();
    this.orderCategorySubscription.unsubscribe();
    this.getDishesSubscription.unsubscribe();
    this.editDishesSubscription.unsubscribe();
    this.createDishesSubscription.unsubscribe();
    this.duplicateDishesSubscription.unsubscribe();
    this.deleteDishesSubscrtiption.unsubscribe();
    this.orderDishesSubscription.unsubscribe();
    this.getModifierSubscription.unsubscribe();
    this.createModifierModalSubscription.unsubscribe();
    this.addModifierModalSubscription.unsubscribe();
    this.createModifierSubscription.unsubscribe();
    this.deleteModifierLocalSubscription.unsubscribe();
    this.editModfierSubscription.unsubscribe();
  }

  private updateMenu() {
    this.getMenuSubscription = this.menuService
      .getMenu(this.restauranteSelected.id)
      .subscribe((response) => {
        this.menuData = response.menus;
        this.menuExpanded(-1);
      });
  }
  public changeForm(id: string) {
    this.create = id;
  }
  // Menu vacÃ­o
  public uploadCsv(e: any) {
    const file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    const fileSize = file.size / 1024;
    if (fileSize > 35840) {
      this.snackBar.open('El archivo excede 35MB', 'Entendido', {
        duration: 15000,
      });
      this.nameFile = '';
      this.uploadMenu.setValue('');
      return;
    } else {
      const zipToSave = new FormData();
      zipToSave.append('templateZip', file);
      this.uploadZipSubscription = this.menuService
        .uploadZip(zipToSave, this.restauranteSelected.id)
        .subscribe(() => {
          this.nameFile = file.name;
          this.snackBar.open('Archivo subido con Ã©xito', 'Entendido', {
            duration: 15000,
          });
          this.ngOnInit();
        });
    }
  }
  // Menu
  public createMenu(dataDuplicate: any): void {
    const openModal = this.dialog.open(NewMenuComponent, {
      panelClass: 'dialog-exit',
      data: {
        restaurantId: this.restauranteSelected.id,
        editMenu: dataDuplicate,
      },
      maxHeight: '85vh',
    });
    this.newMenuModalSubscription = openModal
      .afterClosed()
      .subscribe((reload: boolean) => {
        if (reload) {
          this.updateMenu();
        }
      });
  }

  public openInstructionsModal(): void {
    this.dialog.open(MenuOnboardingComponent, {
      panelClass: 'dialog-exit',
    });
  }

  public duplicateMenu(id: number) {
    this.duplicateMenuSubscription = this.menuService
      .duplicateMenu(this.menuData[id].id)
      .subscribe((renponse) => {
        this.updateMenu();
      });
  }
  public duplicateMenuMatriz() {
    this.duplicateMatrixMenuSubscription = this.menuService
      .duplicateMatrizMenu(this.restauranteSelected.id)
      .subscribe((renponse) => {
        this.updateMenu();
      });
  }
  public deleteMenu(id: number) {
    const dialog = this.dialog.open(DeleteMenuComponent, {
      panelClass: 'dialog-exit',
    });
    this.deleteMenuModalSubscription = dialog
      .afterClosed()
      .subscribe((result) => {
        if (result) {
          this.menuService
            .deleteMenu(this.menuData[id].id)
            .subscribe((response) => {
              this.updateMenu();
            });
        }
      });
  }
  public menuExpanded(i: number) {
    this.menuExpandedId = i;
  }
  public drop(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.menuData, event.previousIndex, event.currentIndex);
    const positions: Array<any> = [];
    for (let i = 0; i < this.menuData.length; i++) {
      positions.push({
        id: this.menuData[i].id,
        position: i + 1,
      });
    }
    this.orderMenuSubscription = this.menuService
      .orderMenu(this.restauranteSelected.id, { positions })
      .subscribe((response) => {
        this.updateMenu();
      });
  }
  // Categoria
  public selectMenus(menuId: number) {
    if (!this.menuData[menuId].category) {
      this.getCategory(menuId);
    }
    this.changeForm('menu');
  }
  private getCategory(menuId: number) {
    this.getCategorySubscription = this.menuService
      .getCategory(this.menuData[menuId].id)
      .subscribe((response) => {
        this.menuData[menuId].category = response.categories;
        this.categorySelected = -1;
      });
  }
  public createCategory(id: number, categoryItem?: any) {
    const dialog = this.dialog.open(NewCategoryComponent, {
      panelClass: 'dialog-exit',
      data: {
        menuID: this.menuData[id].id,
        category: categoryItem,
      },
    });
    this.newCategoryModalSubscription = dialog
      .afterClosed()
      .subscribe((result) => {
        if (result) {
          this.getCategory(id);
        }
      });
  }
  public duplicateCategory(idCategory: number) {
    this.duplicateCategorySubscription = this.menuService
      .duplicateCategory(idCategory)
      .subscribe((data) => {
        this.getCategory(this.menuExpandedId);
      });
  }
  public deleteCategory(menuId: number, categoryId: number) {
    this.deleteCategorySubscription = this.menuService
      .deleteCategory(this.menuData[menuId].category[categoryId].id)
      .subscribe((data) => {
        this.getCategory(menuId);
      });
  }
  public dropCategory(event: CdkDragDrop<string[]>) {
    moveItemInArray(
      this.menuData[this.menuExpandedId].category,
      event.previousIndex,
      event.currentIndex
    );
    const positions: Array<any> = [];
    for (
      let i = 0;
      i < this.menuData[this.menuExpandedId].category.length;
      i++
    ) {
      positions.push({
        id: this.menuData[this.menuExpandedId].category[i].id,
        position: i + 1,
      });
    }
    this.orderCategorySubscription = this.menuService
      .orderCategory(this.menuData[this.menuExpandedId].id, { positions })
      .subscribe((response) => {
        this.getCategory(this.menuExpandedId);
      });
  }
  // Platillo
  public selectCategory(categoryId: number) {
    this.categorySelected = categoryId;
    if (!this.menuData[this.menuExpandedId].category[categoryId].foodDishes) {
      this.getDishes(categoryId);
    }
    this.changeForm('menu');
  }
  private getDishes(categoryId: number) {
    this.getDishesSubscription = this.menuService
      .getDishes(this.menuData[this.menuExpandedId].category[categoryId].id)
      .subscribe((response) => {
        this.menuData[this.menuExpandedId].category[categoryId].foodDishes =
          response.dishes;
      });
  }
  public createDish() {
    const infoDish = this.orderInfoDish();
    const infoDishFormData = new FormData();
    infoDishFormData.append('name', infoDish.name);
    infoDishFormData.append('price_in_cents', infoDish.price_in_cents);
    infoDishFormData.append('iva', infoDish.iva);
    infoDishFormData.append('description', infoDish.description);
    infoDishFormData.append('image', infoDish.image);
    const modifiersId = this.enlistedModifiers.map(
      (modifier: Modifiers) => modifier.id
    );
    infoDishFormData.append('modifiers', '[' + modifiersId.toString() + ']');
    if (
      this.menuData[this.menuExpandedId].category[this.categorySelected]
        .foodDishes[this.dishSelected]
    ) {
      this.editDishesSubscription = this.menuService
        .editDishes(
          infoDishFormData,
          this.menuData[this.menuExpandedId].category[this.categorySelected]
            .foodDishes[this.dishSelected].id
        )
        .subscribe((data) => {
          this.dishSelected =
            this.menuData[this.menuExpandedId].category[this.categorySelected]
              .foodDishes.length - 1;
          this.getDishes(this.categorySelected);
        });
    } else {
      if (this.formFoodDish.invalid) {
        return;
      }
      this.createDishesSubscription = this.menuService
        .createDishes(
          infoDishFormData,
          this.menuData[this.menuExpandedId].category[this.categorySelected].id
        )
        .subscribe((data) => {
          this.getDishes(this.categorySelected);
        });
    }
  }
  private orderInfoDish(): any {
    const data = this.formFoodDish.value;
    data.price_in_cents *= 100;
    if (data.image === '') {
      delete data.image;
    }
    return data;
  }
  public duplicateDish(dishId: number) {
    this.duplicateDishesSubscription = this.menuService
      .duplicateDishes(
        this.menuData[this.menuExpandedId].category[this.categorySelected]
          .foodDishes[dishId].id
      )
      .subscribe((data) => {
        this.getDishes(this.categorySelected);
      });
  }
  public deleteDish(dishId: number) {
    this.deleteDishesSubscrtiption = this.menuService
      .deleteDishes(
        this.menuData[this.menuExpandedId].category[this.categorySelected]
          .foodDishes[dishId].id
      )
      .subscribe((data) => {
        this.getDishes(this.categorySelected);
      });
  }
  public selectDish(dishId: number) {
    this.dishSelected = dishId;
    this.changedataFoodDish(
      this.menuData[this.menuExpandedId].category[this.categorySelected]
        .foodDishes[dishId]
    );
  }
  public uploadImageFoodDish(e: any) {
    const file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    if (!file) {
      return;
    }
    const fileSize = file.size / 1024;
    if (fileSize > 1000) {
      this.snackBar.open('El archivo excede 1MB', 'Entendido', {
        duration: 15000,
      });
      this.formFoodDish.get('image').setValue('');
      return;
    }

    const reader = new FileReader();
    reader.onload = (eImage: any) => {
      const img = new Image();
      img.src = eImage.target.result;
      img.onload = () => {
        if (img.width > 600) {
          this.snackBar.open('El archivo excede 600px de ancho', 'Entendido', {
            duration: 15000,
          });
          return;
        } else {
          if (img.height > 400) {
            this.snackBar.open('El archivo excede 400px de alto', 'Entendido', {
              duration: 15000,
            });
            return;
          }
        }
        this.foodDishPreview = eImage.target.result;
        this.formFoodDish.get('image').setValue(file);
      };
    };
    reader.readAsDataURL(file);
  }
  public changedataFoodDish(FoodDish?: FoodDishes) {
    this.formFoodDish.reset();
    this.enlistedModifiers = FoodDish ? FoodDish.group_modifiers : [];
    this.formFoodDish.get('name').setValue(FoodDish ? FoodDish.name : '');
    this.formFoodDish.get('name').markAsPristine();
    this.formFoodDish
      .get('price_in_cents')
      .setValue(FoodDish ? FoodDish.price_in_cents / 100 : '');
    this.formFoodDish.get('price_in_cents').markAsPristine();
    this.formFoodDish.get('iva').setValue(FoodDish ? FoodDish.iva : '');
    this.formFoodDish.get('iva').markAsPristine();
    this.formFoodDish
      .get('description')
      .setValue(FoodDish ? FoodDish.description : '');
    this.formFoodDish.get('description').markAsPristine();
    this.foodDishPreview = FoodDish ? FoodDish.image : null;
    if (!FoodDish) {
      this.dishSelected = -1;
    }
  }
  public dropFoodDishes(event: CdkDragDrop<string[]>) {
    moveItemInArray(
      this.menuData[this.menuExpandedId].category[this.categorySelected]
        .foodDishes,
      event.previousIndex,
      event.currentIndex
    );
    const positions: Array<any> = [];
    for (
      let i = 0;
      i <
      this.menuData[this.menuExpandedId].category[this.categorySelected]
        .foodDishes.length;
      i++
    ) {
      positions.push({
        id: this.menuData[this.menuExpandedId].category[this.categorySelected]
          .foodDishes[i].id,
        position: i + 1,
      });
    }
    this.orderDishesSubscription = this.menuService
      .orderDishes(
        this.menuData[this.menuExpandedId].category[this.categorySelected].id,
        { positions }
      )
      .subscribe((response) => {
        this.getDishes(this.categorySelected);
      });
  }
  // Modificadores
  public getModifier() {
    this.getModifierSubscription = this.menuService
      .getModifier(this.restauranteSelected.id)
      .subscribe((data) => {
        this.modifiers = data.modifiers;
      });
  }
  public selectModifier(modifier: Modifiers): void {
    this.modifiersSelected = modifier;
    if (modifier) {
      this.formModifier.reset();
      this.formModifier.patchValue({
        name: this.modifiersSelected.name,
        question: this.modifiersSelected.optional_ask
          ? 'Opcional'
          : 'Obligatorio',
        multipleChoice: this.modifiersSelected.multiple_option ? 'SÃ­' : 'No',
        min: this.modifiersSelected.min_options_per_user,
        max: this.modifiersSelected.max_options_per_user,
      });
      this.modifiersInForm = this.formModifier.get(
        'modifiersInForm'
      ) as FormArray;
      while (this.modifiersInForm.length !== 0) {
        this.modifiersInForm.removeAt(0);
      }
      for (const modifierElement of modifier.modifiers) {
        this.addModifierItem({
          name: modifierElement.name,
          price_in_cents: Number(modifierElement.price_in_cents / 100).toFixed(
            2
          ),
        });
      }
    }
  }
  public createModifier() {
    const openModal = this.dialog.open(CreateModifierComponent, {
      panelClass: 'dialog-exit',
      data: {
        restaurantId: this.restauranteSelected.id,
      },
      maxHeight: '85vh',
    });
    this.createModifierModalSubscription = openModal
      .afterClosed()
      .subscribe((reload: boolean) => {
        if (reload) {
          this.getModifier();
        }
      });
  }
  public addModifier() {
    const modifierDialog = this.dialog.open(AddModifierComponent, {
      panelClass: 'dialog-exit',
      data: {
        modifiers: this.enlistedModifiers,
      },
    });
    this.addModifierModalSubscription = modifierDialog
      .afterClosed()
      .subscribe((modifier: Modifiers) => {
        if (modifier) {
          this.enlistedModifiers.push(modifier);
        }
      });
  }
  public duplicateModifier(modifierId: number) {
    this.createModifierSubscription = this.menuService
      .createModifier(
        this.orderInfoDish(),
        this.menuData[this.menuExpandedId].category[this.categorySelected].id
      )
      .subscribe((data) => {});
  }
  public deleteModifier(modifierId: number) {
    this.enlistedModifiers.splice(modifierId, 1);
  }
  public changedataModifier(modifier?: Modifiers) {
    this.formFoodDish.get('name').setValue(modifier ? modifier.name : '');
  }
  public manageControlQuantityByOne(
    form: FormGroup,
    controlName: string,
    operation: string
  ): void {
    const actualValue = form.get(controlName).value;
    let newValue = actualValue;
    if (operation === 'subtract') {
      if (actualValue > 1) {
        newValue = actualValue - 1;
      }
    } else if (operation === 'add') {
      newValue = actualValue + 1;
    }
    form.get(controlName).setValue(newValue);
  }

  public editModfier(mod: any) {
    const openModal = this.dialog.open(CreateModifierComponent, {
      panelClass: 'dialog-exit',
      data: {
        restaurantId: this.restauranteSelected.id,
        modifier: mod,
      },
    });
    this.createModifierModalSubscription = openModal
      .afterClosed()
      .subscribe((reload: boolean) => {
        if (reload) {
          this.getModifier();
        }
      });
  }
  public deleteLocalModifier(modifierId: number) {
    this.deleteModifierLocalSubscription = this.menuService
      .deleteModifierLocal(modifierId)
      .subscribe(() => {
        this.getModifier();
      });
  }
  public addModifierItem(value?: any): void {
    if (this.formModifier.get('modifiersInForm').invalid) {
      return;
    }
    this.modifiersInForm = this.formModifier.get(
      'modifiersInForm'
    ) as FormArray;
    this.modifiersInForm.push(this.addModifiers(value));
  }
  public addModifiers(value?: any): FormGroup {
    const nameToAssign = value ? value.name : '';
    const priceToAssign = value ? value.price_in_cents : '';
    return this.formBuilder.group({
      name: new FormControl(nameToAssign, [
        Validators.required,
        Validators.minLength(3),
        Validators.maxLength(100),
      ]),
      price_in_cents: new FormControl(priceToAssign, [
        Validators.required,
        Validators.min(0),
        Validators.pattern(/^[+]?([0-9]{0,})*[.]?([0-9]{0,2})?$/),
      ]),
    });
  }
  get getModifiers(): any {
    return (this.formModifier.get('modifiersInForm') as FormArray).controls;
  }
  removeModifier(index: number): void {
    this.modifiersInForm = this.formModifier.get(
      'modifiersInForm'
    ) as FormArray;
    this.modifiersInForm.removeAt(index);
  }
  updateModifier(): void {
    if (this.formModifier.valid) {
      const data = {
        name: this.formModifier.get('name').value,
        optional_ask: this.formModifier.get('question').value === 'Opcional',
        multiple_option: this.formModifier.get('multipleChoice').value === 'SÃ­',
        min_options_per_user: this.formModifier.get('min').value,
        max_options_per_user: this.formModifier.get('max').value,
        modifiers: Array<any>(),
      };
      for (const item of this.formModifier.get('modifiersInForm').value) {
        data.modifiers.push({
          name: item.name,
          price_in_cents: Math.round(item.price_in_cents * 100),
        });
      }
      this.editModfierSubscription = this.menuService
        .editModifier(data, this.modifiersSelected.id)
        .subscribe(() => {
          this.getModifier();
          this.snackBar.open(
            'Se ha actualizado la informaciÃ³n del modificador',
            'Entendido',
            {
              duration: 15000,
            }
          );
          this.selectModifier(undefined);
        });
    }
  }
  public getModifiersById(modifierId: number): Modifiers {
    for (const modifier of this.modifiers) {
      if (modifier.id === modifierId) {
        return modifier;
      }
    }
    return null;
  }
}
