import { Component, Inject, OnInit, OnDestroy } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import {
  FormGroup,
  FormControl,
  Validators,
  FormBuilder,
  FormArray,
} from '@angular/forms';
import { MenuService } from '@services';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-create-modifier',
  templateUrl: './create-modifier.component.html',
  styleUrls: ['../new-menu/new-menu.component.scss'],
})
export class CreateModifierComponent implements OnInit, OnDestroy {
  public submit = false;
  public formModifier = new FormGroup({
    name: new FormControl('', [
      Validators.required,
      Validators.minLength(3),
      Validators.maxLength(40),
    ]),
    optional_ask: new FormControl('', Validators.required),
    multiple_option: new FormControl('', Validators.required),
    max_options_per_user: new FormControl(1, [
      Validators.required,
      Validators.min(1),
    ]),
    min_options_per_user: new FormControl(1, [
      Validators.required,
      Validators.min(1),
    ]),
    modifiers: this.formBuilder.array([]),
  });
  public modifiers: FormArray;
  private editSubscription = Subscription.EMPTY;
  private createSubscription = Subscription.EMPTY;

  constructor(
    private menuService: MenuService,
    private formBuilder: FormBuilder,
    public dialogRef: MatDialogRef<CreateModifierComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {}

  ngOnInit() {
    if (this.data.modifier) {
      this.setModifierValues();
    } else {
      this.addModifier();
    }
  }

  ngOnDestroy() {
    this.editSubscription.unsubscribe();
    this.createSubscription.unsubscribe();
  }

  public setModifierValues() {
    const modifier = this.data.modifier;
    this.formModifier.get('name').setValue(modifier.name);
    const optionalAskValue = modifier.optional_ask ? 'si' : 'no';
    this.formModifier.get('optional_ask').setValue(optionalAskValue);
    const multipleOptionValue = modifier.multiple_option ? 'si' : 'no';
    this.formModifier.get('multiple_option').setValue(multipleOptionValue);
    this.formModifier
      .get('max_options_per_user')
      .setValue(modifier.max_options_per_user);
    this.formModifier
      .get('min_options_per_user')
      .setValue(modifier.min_options_per_user);
    for (const modifierElement of modifier.modifiers) {
      this.addModifier({
        name: modifierElement.name,
        price_in_cents: modifierElement.price_in_cents,
      });
    }
  }

  onNoClick(): void {
    this.closeModal();
  }
  public closeModal(reload: boolean = false) {
    this.dialogRef.close(reload);
  }
  public addModifier(value?: any) {
    if (this.formModifier.get('modifiers').invalid) {
      return;
    }
    this.modifiers = this.formModifier.get('modifiers') as FormArray;
    this.modifiers.push(this.addModifiers(value));
  }
  public createModifier() {
    this.submit = true;
    if (this.formModifier.invalid) {
      return;
    }
    const data = {
      name: this.formModifier.get('name').value,
      optional_ask: this.formModifier.get('optional_ask').value === 'si',
      multiple_option: this.formModifier.get('multiple_option').value === 'si',
      min_options_per_user: this.formModifier.get('min_options_per_user').value,
      max_options_per_user: this.formModifier.get('max_options_per_user').value,
      modifiers: Array<any>(),
    };
    for (const item of this.formModifier.get('modifiers').value) {
      data.modifiers.push({
        name: item.name,
        price_in_cents: Math.round(item.price_in_cents * 100),
      });
    }
    if (this.data.modifier) {
      this.editSubscription = this.menuService
        .editModifier(data, this.data.modifier.id)
        .subscribe(() => {
          this.closeModal(true);
        });
    } else {
      this.createSubscription = this.menuService
        .createModifier(data, this.data.restaurantId)
        .subscribe(() => {
          this.closeModal(true);
        });
    }
  }
  public manageControlQuantityByOne(
    form: FormGroup,
    controlName: string,
    operation: string
  ): void {
    const actualValue = form.get(controlName).value;
    let newValue = actualValue;
    if (operation === 'subtract') {
      if (actualValue > 1) {
        newValue = actualValue - 1;
      }
    } else if (operation === 'add') {
      newValue = actualValue + 1;
    }
    form.get(controlName).setValue(newValue);
  }
  public addModifiers(value?: any): FormGroup {
    const nameToAssign = value ? value.name : '';
    const priceToAssign = value ? value.price_in_cents : '';
    return this.formBuilder.group({
      name: new FormControl(nameToAssign, [
        Validators.required,
        Validators.minLength(3),
        Validators.maxLength(30),
      ]),
      price_in_cents: new FormControl(priceToAssign, [
        Validators.required,
        Validators.min(0),
        Validators.pattern(/^[+]?([0-9]{0,})*[.]?([0-9]{0,2})?$/),
      ]),
    });
  }
  get getModifiers() {
    return (this.formModifier.get('modifiers') as FormArray).controls;
  }

  removeModifier(index: number) {
    this.modifiers = this.formModifier.get('modifiers') as FormArray;
    this.modifiers.removeAt(index);
  }
}
