import { Component, Inject, OnInit, OnDestroy } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Validators, FormControl } from '@angular/forms';
import { MenuService } from '@services';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-new-category',
  templateUrl: './new-category.component.html',
  styleUrls: ['../new-menu/new-menu.component.scss']
})
export class NewCategoryComponent implements OnInit, OnDestroy {
  public name = new FormControl('', [
    Validators.required,
    Validators.minLength(3),
    Validators.maxLength(100)
  ]);
  public edit = false;
  private editSubscription = Subscription.EMPTY;
  private createSubscription = Subscription.EMPTY;

  constructor(
    private menuService: MenuService,
    public dialogRef: MatDialogRef<NewCategoryComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {}

  ngOnInit() {
    if (this.data.category) {
      this.edit = true;
      this.name.setValue(this.data.category.name);
    }
  }

  ngOnDestroy() {
    this.editSubscription.unsubscribe();
    this.createSubscription.unsubscribe();
  }

  onNoClick(): void {
    this.closeModal();
  }
  public closeModal(reload: boolean = false) {
    this.dialogRef.close(reload);
  }

  public createCategory() {
    if (this.name.invalid) {
      return;
    }
    if (this.edit) {
      this.editSubscription = this.menuService
        .editCategory(
          {
            name: this.name.value
          },
          this.data.category.id
        )
        .subscribe(data => {
          this.closeModal(true);
        });
    } else {
      this.createSubscription = this.menuService
        .createCategory(
          {
            name: this.name.value
          },
          this.data.menuID
        )
        .subscribe(data => {
          this.closeModal(true);
        });
    }
  }
}
