import { Component, Inject, OnInit, OnDestroy } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Validators, FormBuilder } from '@angular/forms';
import { MenuService, RestaurantService } from '@services';
import { Restaurant, ResponseModifiers, Modifiers } from '@models';
import { Subscription } from 'rxjs';
@Component({
  selector: 'app-add-modifier',
  templateUrl: './add-modifier.component.html',
  styleUrls: [
    '../new-menu/new-menu.component.scss',
    './add-modifier.component.scss'
  ]
})
export class AddModifierComponent implements OnInit, OnDestroy {
  public modifiersGroups: Array<Modifiers> = [];
  public modifierForm = this.fb.group({
    modifierGroup: [null, Validators.required]
  });
  private restaurantSubscription = Subscription.EMPTY;
  private modifierSubscription = Subscription.EMPTY;

  constructor(
    public dialogRef: MatDialogRef<AddModifierComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private menuService: MenuService,
    private restaurantService: RestaurantService,
    private fb: FormBuilder
  ) {}

  ngOnInit(): void {
    this.initRestaurantListener();
  }

  ngOnDestroy() {
    this.restaurantSubscription.unsubscribe();
    this.modifierSubscription.unsubscribe();
  }

  initRestaurantListener(): void {
    this.restaurantSubscription = this.restaurantService.currentRestaurant.subscribe(
      (restaurant: Restaurant) => {
        this.modifierSubscription = this.menuService
          .getModifier(restaurant.id)
          .subscribe((response: ResponseModifiers) => {
            this.modifiersGroups = response.modifiers.filter(
              (group: Modifiers) => {
                for (const modifier of this.data.modifiers) {
                  if (modifier.id === group.id) {
                    return false;
                  }
                }
                return true;
              }
            );
          });
      }
    );
  }

  public closeModal(): void {
    this.dialogRef.close();
  }

  public addModifier(): void {
    if (this.modifierForm.valid) {
      this.dialogRef.close(this.modifierForm.get('modifierGroup').value);
    }
  }
}
