import { Component, Inject, OnInit, OnDestroy } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormGroup, Validators, FormControl } from '@angular/forms';
import { MenuService } from '@services';
import { OwlOptions } from 'ngx-owl-carousel-o';
import { Regex24Hours } from '@constants';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-new-menu',
  templateUrl: './new-menu.component.html',
  styleUrls: ['./new-menu.component.scss'],
})
export class NewMenuComponent implements OnInit, OnDestroy {
  public createMenuForm = new FormGroup({
    name: new FormControl('', [
      Validators.required,
      Validators.minLength(3),
      Validators.maxLength(30),
    ]),
    from: new FormControl('00:00', [
      Validators.required,
      Validators.pattern(Regex24Hours),
    ]),
    to: new FormControl('00:00', [
      Validators.required,
      Validators.pattern(Regex24Hours),
    ]),
    icon: new FormControl('', Validators.required),
  });
  public customOptions: OwlOptions = {
    dots: false,
    navSpeed: 700,
    items: 5,
    navText: ['', ''],
    nav: true,
  };
  private edit = false;
  public icons: Array<any>;
  iconsSubscription = Subscription.EMPTY;
  editSubscription = Subscription.EMPTY;
  createSubscription = Subscription.EMPTY;

  constructor(
    public dialogRef: MatDialogRef<NewMenuComponent>,
    private menuService: MenuService,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {}

  ngOnInit(): void {
    if (this.data.editMenu) {
      this.edit = true;
      this.createMenuForm.get('name').setValue(this.data.editMenu.name);
      this.createMenuForm.get('from').setValue(this.data.editMenu.from);
      this.createMenuForm.get('to').setValue(this.data.editMenu.to);
      this.createMenuForm.get('icon').setValue(this.data.editMenu.icon);
    }
    this.getIcons();
  }

  ngOnDestroy() {
    this.iconsSubscription.unsubscribe();
    this.editSubscription.unsubscribe();
    this.createSubscription.unsubscribe();
  }

  onNoClick(): void {
    this.closeModal();
  }

  private getIcons() {
    this.iconsSubscription = this.menuService
      .getIcons()
      .subscribe((response) => {
        this.icons = response.icons;
      });
  }
  public closeModal(reload: boolean = false) {
    this.dialogRef.close(reload);
  }
  public createMenu() {
    this.createMenuForm.get('icon').markAsTouched();
    if (this.createMenuForm.invalid) {
      return;
    }
    if (this.edit) {
      this.editSubscription = this.menuService
        .editMenu(this.createMenuForm.value, this.data.editMenu.id)
        .subscribe((data) => {
          this.closeModal(true);
        });
    } else {
      this.createSubscription = this.menuService
        .createMenu(this.createMenuForm.value, this.data.restaurantId)
        .subscribe((data) => {
          this.closeModal(true);
        });
    }
  }
  public selectIconMenu(id: number) {
    this.createMenuForm.get('icon').setValue(id);
  }

  /**
   * Tells if the error on the icon carousel has to be visible.
   */
  public get isIconSelectionInvalid(): boolean {
    const iconControl = this.createMenuForm.get('icon');
    return iconControl.touched && iconControl.hasError('required');
  }
}
