import { MatDialogRef } from '@angular/material/dialog';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-menu-onboarding',
  templateUrl: './menu-onboarding.component.html',
  styleUrls: ['./menu-onboarding.component.scss']
})
export class MenuOnboardingComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<MenuOnboardingComponent>,
  ) { }

  ngOnInit(): void { }

  public closeModal(): void {
    this.dialogRef.close();
  }

}
