import { Component, OnInit, OnDestroy } from '@angular/core';
import { RestaurantService, FranchiseService } from '@services';
import { Restaurant, Franchise, ResponseRestaurant } from '@models';
import { FormBuilder } from '@angular/forms';
import { version } from '../../../../package.json';
import { Subscription } from 'rxjs';
import { IRestaurant } from '@interfaces';
import { map } from 'rxjs/operators';
@Component({
  selector: 'app-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.scss']
})
export class LayoutComponent implements OnInit, OnDestroy {
  public restaurants: Array<Restaurant>;
  public restaurantSelection = this.fb.group({
    restaurant: [null]
  });
  public version: string = version;
  private restaurantSubscription = Subscription.EMPTY;
  private franchiseSubscription = Subscription.EMPTY;
  private restaurantsSubscription = Subscription.EMPTY;

  constructor(
    private fb: FormBuilder,
    private restaurantService: RestaurantService,
    private franchiseService: FranchiseService
  ) {}

  ngOnInit(): void {
    this.initRestaurantListener();
  }

  ngOnDestroy() {
    this.restaurantSubscription.unsubscribe();
    this.franchiseSubscription.unsubscribe();
    this.restaurantsSubscription.unsubscribe();
  }

  initRestaurantListener(): void {
    this.restaurantSubscription = this.restaurantService.currentRestaurant.subscribe(
      (restaurant: Restaurant) => {
        this.restaurantSelection.get('restaurant').setValue(restaurant.id);
        this.franchiseSubscription = this.franchiseService.currentFranchise.subscribe(
          (franchise: Franchise) => {
            this.restaurantsSubscription = this.restaurantService
              .getRestaurants(franchise.id)
              .pipe(
                map((response: ResponseRestaurant) => {
                  const restaurants = response.restaurants.map(
                    (pRestaurant: IRestaurant) => {
                      return new Restaurant(pRestaurant);
                    }
                  );

                  return restaurants;
                })
              )
              .subscribe((restaurants: Array<Restaurant>) => {
                this.restaurants = restaurants;
              });
          }
        );
      }
    );
  }

  public restaurantChanged(restaurantId: number): void {
    localStorage.setItem('restaurantId', JSON.stringify(restaurantId));
    location.reload();
  }
}
